//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

// Avenir
@font-face {
	@include fontdefault(normal, 900, 'Avenir');
	@include fontpath('Avenir Heavy', 'Avenir-Heavy', Avenir-Heavy); }
@font-face {
	@include fontdefault(italic, 900, 'Avenir');
	@include fontpath('Avenir Heavy Oblique', 'Avenir-HeavyOblique', Avenir-HeavyOblique); }

@font-face {
	@include fontdefault(normal, 800, 'Avenir');
	@include fontpath('Avenir Heavy', 'Avenir-Heavy', Avenir-Heavy); }
@font-face {
	@include fontdefault(italic, 800, 'Avenir');
	@include fontpath('Avenir Heavy Oblique', 'Avenir-HeavyOblique', Avenir-HeavyOblique); }

@font-face {
	@include fontdefault(normal, 500, 'Avenir');
	@include fontpath('Avenir Medium', 'Avenir-Medium', Avenir-Medium); }
@font-face {
	@include fontdefault(italic, 500, 'Avenir');
	@include fontpath('Avenir Medium Oblique', 'Avenir-MediumOblique', Avenir-MediumOblique); }

@font-face {
	@include fontdefault(normal, 400, 'Avenir');
	@include fontpath('Avenir Book', 'Avenir-Book', Avenir-Book); }
@font-face {
	@include fontdefault(italic, 400, 'Avenir');
	@include fontpath('Avenir Oblique', 'Avenir-Oblique', Avenir-Oblique); }

@font-face {
	@include fontdefault(normal, 300, 'Avenir');
	@include fontpath('Avenir Light', 'Avenir-Light', Avenir-Light); }
@font-face {
	@include fontdefault(italic, 300, 'Avenir');
	@include fontpath('Avenir Light Oblique', 'Avenir-LightOblique', Avenir-LightOblique); }

