.content {
	padding: 80px 0;
	@include sm {
		padding: 40px 0; }
	.title-lg,
	h1 {
		margin-bottom: 30px;
		text-align: center; }
	.title-md,
	h2 {
		text-align: center;
		margin-bottom: 20px; }
	h1 {
		font-family: $av;
		@include f(48px,500,1);
		font-size: 48px;
		@include sm {
			@include f(36px,900,1.36); } }
	h2 {
		font-family: $av;
		@include f(36px,900,1.36);
		@include sm {
			@include f(32px,null,1.5); } }
	p {
		&:not(:last-child) {
			margin-bottom: 20px; } } }
