  .card {
    padding: 24px 10px 65px;
  }
}

.card:not(:first-child) {
  margin-top: 40px;
}

.cards .card.is-show {
  opacity: 1;
  visibility: visible;
}

.cards .card {
  opacity: 0;
  visibility: hidden;
  transition: opacity .2s visibility .2s;
}

.card.is-open {
  height: auto;
}

.card__wrap {
  display: table;
  width: 100%;
