select.select {
  display: none;
}

.select {
  position: relative;
  padding-top: 48px;
  font-size: 16px;
}

.select.sol-active.sol-selection-bottom .sol-inner-container {
  border-bottom-right-radius: 12px;
}

.select.sol-active .sol-inner-container {
  box-shadow: none;
  border: solid 1px #30a1ff;
}

.select.sol-active .sol-selection-container {
  z-index: 9998;
  margin: -12px 0 0 0;
  padding: 20px 8px 5px 0;
  border: solid 1px #30a1ff;
}

.select .sol-inner-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 48px;
  line-height: 48px;
  border-radius: 12px 2px;
  border: solid 1px #cecdd1;
}

.select .sol-input-container {
  right: 0;
}

.select .sol-input-container input {
  padding: 13px 16px;
  font-family: "Avenir", Arial, sans-serif;
  font-size: 16px;
}

.select .sol-caret-container {
  display: none;
}

.select .sol-selection-container {
  padding-right: 8px;
  background-color: #fff;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2);
}

.select .sol-selection::-webkit-scrollbar {
  width: 4px;
}

.select .sol-selection::-webkit-scrollbar-track {
  background-color: #fff;
}

.select .sol-selection::-webkit-scrollbar-thumb {
  background-color: #9c9ba2;
  width: 4px;
  border-radius: 2px;
}

.select .sol-checkbox {
  opacity: 0;
}

.select .sol-checkbox:checked + .sol-label-text {
  color: #30a1ff;
}

.select .sol-checkbox:checked + .sol-label-text:before {
  border-color: #30a1ff;
  background-color: #30a1ff;
}

.select .sol-label {
  padding: 0 10px;
  user-select: none;
  cursor: pointer;
}

.select .sol-label:before {
  position: absolute;
  content: "";
  top: 4px;
  left: 19px;
  transform-origin: top right;
  width: 10px;
  height: 2px;
  transform: rotate(-45deg);
  z-index: 1;
  background-color: #fff;
}

.select .sol-label:after {
  position: absolute;
  content: "";
  top: 11px;
  left: 19px;
  width: 5px;
  height: 2px;
  transform-origin: top right;
  transform: rotate(45deg);
  background-color: #fff;
}

.select .sol-label-text {
  padding-left: 32px;
}

.select .sol-label-text:before {
  position: absolute;
  content: "";
  top: 0;
  left: 17px;
  width: 16px;
  height: 16px;
  border-radius: 2px;
  transition: border-color .3s, background-color .3s;
  border: 2px solid #6b6a74;
}

.select .sol-option {
  transition: color .3s;
  line-height: 1;
}

.select .sol-option:not(:first-child) {
  margin-top: 16px;
}

.select .sol-option.keyboard-selection,
.select .sol-selection:not(.sol-keyboard-navigation) .sol-option:hover {
  background: transparent;
  color: #30a1ff;
}

.select .sol-option.keyboard-selection .sol-label-text:before,
.select .sol-selection:not(.sol-keyboard-navigation) .sol-option:hover .sol-label-text:before {
  border-color: #30a1ff;
}

.select .sol-results-count,
.select .sol-selected-display-item {
  position: relative;
  margin: 4px 8px 4px 0;
  border: none;
  color: #fff;
  font-family: "Avenir", Arial, sans-serif;
  font-size: 16px;
  line-height: 1;
  border-radius: 28px;
}

.select .sol-selected-display-item-text {
  padding: 8px 10px 8px 27px;
}

.select .sol-quick-delete {
  position: absolute;
  top: 9px;
  left: 8px;
  width: 14px;
  height: 14px;
  padding: 0;
  font-size: 15px;
  line-height: 14px;
  text-align: center;
  border-radius: 50%;
  background-color: #fff;
}

.select .sol-current-selection {
  margin-top: 16px;
}

.select_engagement_model .sol-results-count,
.select_engagement_model .sol-selected-display-item {
  background-color: #17dcae;
}

.select_engagement_model .sol-quick-delete {
  color: #17dcae;
}

.select_domain .sol-results-count,
.select_domain .sol-selected-display-item {
  background-color: #30a1ff;
}

.select_domain .sol-quick-delete {
  color: #30a1ff;
}

.select_technology .sol-results-count,
.select_technology .sol-selected-display-item {
  background-color: #573eba;
}

.select_technology .sol-quick-delete {
  color: #573eba;
}

